import axios from '../axios'

/**
 * 关于通道的增删改查
 * @param {*} data
 */

// 获取所有通道信息
export const getChannelList = (params) => {
  return axios({
    url: '/api/v1/getChannelList',
    method: 'get',
    params
  })
}

// 获取单个通道信息
export const getChannelInfo = (params) => {
  return axios({
    url: '/api/v1/getChannelInfo',
    method: 'get',
    params
  })
}

// 新增通道
export const addChannel = (params) => {
  return axios({
    url: '/api/v1/addChannel',
    method: 'get',
    params
  })
}

// 修改通道
export const updateChannel = (params) => {
  return axios({
    url: '/api/v1/updateChannel',
    method: 'get',
    params
  })
}

// 删除通道
export const deleteChannel = (params) => {
  return axios({
    url: '/api/v1/deleteChannel',
    method: 'get',
    params
  })
}

// 开启推流
export const openChannel = (params) => {
  return axios({
    url: '/api/v1/openChannel',
    method: 'get',
    params
  })
}

// 关闭推流
export const closeChannel = (params) => {
  return axios({
    url: '/api/v1/closeChannel',
    method: 'get',
    params
  })
}
