<template>
  <div class="boxShadow">
    <div style="margin: 20px">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="名称">
          <el-input v-model="search" placeholder="名称"></el-input>
        </el-form-item>
        <!-- <el-form-item>
          <el-button type="primary" @click="handleSearch">查询</el-button>
        </el-form-item> -->
        <el-form-item>
          <el-button type="primary" @click="handleAdd">新增</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div style="margin-top: 20px">
      <el-table :data="tableData.filter(data => !search || data.name.includes(search)).slice((currentPage - 1)*pageSize, currentPage*pageSize)" border style="width: 100%" align="center">
        <el-table-column prop="indexcode" label="唯一编码" width="200" align="center">
        </el-table-column>
        <el-table-column prop="name" label="名称" width="150" align="center">
        </el-table-column>
        <el-table-column prop="connectType" label="传输模式" width="100" align="center">
        </el-table-column>
        <el-table-column prop="srcURL" label="源地址" min-width="300" align="center">
        </el-table-column>
        <el-table-column prop="dstFormat" label="推流协议" width="100" align="center">
        </el-table-column>
        <el-table-column prop="dstURL" label="推流地址" min-width="300" align="center">
        </el-table-column>
        <el-table-column prop="connectTimeout" label="连接超时时间（秒）" width="150" align="center">
        </el-table-column>
        <el-table-column prop="mediaType" label="媒体类型" min-width="150" align="center">
          <template slot-scope="props">
            <span v-if="props.row.mediaType == 'video'">视频</span>
            <span v-else>视频|音频</span>
          </template>
        </el-table-column>
        <el-table-column prop="enable" label="开启状态" min-width="150" align="center">
          <template slot-scope="props">
            <el-switch v-model="props.row.enable" active-value="true" inactive-value="false" @change="changeStatus($event, props.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150" fixed="right" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog :title="title" :close-on-click-modal="false" :visible.sync="dialogFormVisible" @close="closeDialogFormVisible">
      <el-form :model="form" ref="form" :rules="rules">
        <el-form-item label="唯一编码" :label-width="formLabelWidth">
          <el-input v-model="form.indexCode" disabled></el-input>
        </el-form-item>
        <el-form-item label="名称" :label-width="formLabelWidth" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="传输模式" :label-width="formLabelWidth">
          <el-radio-group v-model="form.connectType">
            <el-radio-button label="tcp"></el-radio-button>
            <el-radio-button label="udp"></el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="源地址" :label-width="formLabelWidth" prop="srcURL">
          <el-input v-model="form.srcURL"></el-input>
        </el-form-item>
        <el-form-item label="推流地址" :label-width="formLabelWidth" prop="dstURL">
          <el-input v-model="form.dstURL">
            <template slot="prepend">rtmp://</template>
          </el-input>
        </el-form-item>
        <el-form-item label="连接超时时间（秒）" :label-width="formLabelWidth">
          <el-select v-model="form.connectTimeout" placeholder="请选择">
            <el-option v-for="item in timeouts" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="媒体类型" :label-width="formLabelWidth">
          <el-select v-model="form.mediaType" placeholder="请选择">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="开启状态" :label-width="formLabelWidth">
          <el-switch v-model="form.enable" active-value="true" inactive-value="false"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit('form')">确 定</el-button>
      </div>
    </el-dialog>
    <div class="block" style="text-align: right; margin-top:10px">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import api from '@/http/api'
export default {
  data () {
    return {
      search: '',
      timeouts: [],
      options: [
        {
          value: 'video',
          label: '视频'
        },
        {
          value: 'video|audio',
          label: '视频|音频'
        }
      ],
      formInline: {
        user: '',
        enable: ''
      },
      offset: 0,
      row: 50,
      tableData: [],
      dialogFormVisible: false,
      form: {
        id: '',
        indexCode: this.generateUUID(),
        name: '',
        connectType: 'tcp',
        srcURL: '',
        dstFormat: 'rtmp',
        dstURL: '',
        connectTimeout: '5',
        mediaType: 'video',
        enable: 'true'
      },
      rules: {
        name: [{ required: true, message: '请输入名称', trigger: 'blur' }],
        srcURL: [{ required: true, message: '请输入源地址', trigger: 'blur' }],
        dstURL: [{ required: true, message: '请输入推流地址', trigger: 'blur' }]
      },
      formLabelWidth: '150px',
      title: '',
      // 默认开始的页面
      currentPage: 1,
      // 每页的数据条数
      pageSize: 10
    }
  },
  methods: {
    init () {
      const param = { offset: this.offset, row: this.row }
      api.channel
        .getChannelList(param)
        .then(res => {
          this.tableData = res.data.ChannelList
        })
        .catch(function (res) {
          // console.log(err)
          this.$message({
            type: 'warning',
            message: '获取列表失败!'
          })
        })
    },
    handleAdd () {
      this.dialogFormVisible = true
      this.title = '新增通道'
    },
    handleEdit (index, row) {
      // console.log(index, row)
      let param = { indexCode: row.indexcode }
      api.channel.getChannelInfo(param).then(res => {
        var form = {
          id: res.data.ChannelInfo.Id,
          indexCode: res.data.ChannelInfo.indexcode,
          name: res.data.ChannelInfo.name,
          srcURL: res.data.ChannelInfo.srcURL,
          dstURL: res.data.ChannelInfo.dstURL.slice(7),
          dstFormat: res.data.ChannelInfo.dstFormat,
          connectType: res.data.ChannelInfo.connectType.toLowerCase(),
          mediaType: res.data.ChannelInfo.mediaType,
          connectTimeout: res.data.ChannelInfo.connectTimeout,
          enable: res.data.ChannelInfo.enable
        }
        this.form = form
      })

      this.dialogFormVisible = true
      this.title = '编辑通道'
    },
    handleDelete (index, row) {
      // console.log(index, row)
      this.$confirm(`确认删除 ${row.name || row.Id}`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      })
        .then(() => {
          let param = { indexCode: row.indexcode }
          this.tableData.splice(index, 1)
          api.channel
            .deleteChannel(param)
            .then(res => {
              // console.log(res.data)
              if (res.data === 'OK') {
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                })
              } else {
                this.$message({
                  type: 'warning',
                  message: '删除失败!'
                })
              }
            })
            .catch(res => {
              // console.log(res)
              this.$message({
                type: 'warning',
                message: '删除失败!'
              })
            })
        })
        .catch(() => {})
    },
    closeDialogFormVisible () {
      // element封装的方法,清空模态框的值
      this.$refs.form.resetFields()
      // 初始化title的值
      // this.title = ''
      // 初始化addForm中的值
      this.form = {
        id: '',
        indexCode: this.generateUUID(),
        name: '',
        connectType: 'tcp',
        srcURL: '',
        dstFormat: 'rtmp',
        dstURL: '',
        connectTimeout: '5',
        mediaType: 'video',
        enable: 'true'
      }
    },
    onSubmit (form) {
      let params = {
        indexCode: this.form.indexCode,
        name: this.form.name,
        srcURL: '"' + this.form.srcURL + '"',
        connectType: this.form.connectType,
        timeout: this.form.connectTimeout,
        mediaType: this.form.mediaType,
        dstURL: 'rtmp://' + this.form.dstURL,
        dstFormat: this.form.dstFormat,
        enable: this.form.enable
      }
      // console.log(params)
      this.$refs[form].validate(valid => {
        if (valid) {
          // 当没有传过来id的时候,说明是添加,所以发送添加请求
          if (!this.form.id) {
            api.channel
              .addChannel(params)
              .then(res => {
                // console.log(res.data)
                if (res.data === 'OK') {
                  this.$message({
                    type: 'success',
                    message: '新增成功!'
                  })
                  this.dialogFormVisible = false
                } else {
                  this.$message({
                    type: 'warning',
                    message: '新增失败!'
                  })
                }
                this.init()
              })
              .catch(res => {
                // console.log(res.data)
                this.$message({
                  type: 'warning',
                  message: '新增失败!'
                })
              })
          } else {
            api.channel
              .updateChannel(params)
              .then(res => {
                // console.log(res.data)
                if (res.data === 'OK') {
                  this.$message({
                    type: 'success',
                    message: '修改成功!'
                  })
                  this.dialogFormVisible = false
                } else {
                  this.$message({
                    type: 'warning',
                    message: '修改失败!'
                  })
                }
                this.init()
              })
              .catch(res => {
                // console.log(res.data)
                this.$message({
                  type: 'warning',
                  message: '修改失败!'
                })
              })
          }
        }
      })
    },
    changeStatus ($event, obj) {
      let params = {
        indexCode: obj.indexcode,
        name: obj.name,
        srcURL: '"' + obj.srcURL + '"',
        connectType: obj.connectType,
        timeout: obj.connectTimeout,
        mediaType: obj.mediaType,
        dstURL: obj.dstURL,
        dstFormat: obj.dstFormat,
        enable: obj.enable
      }
      var _this = this
      let enable = obj.enable
      if (enable === 'true') {
        // console.log('调用开启推流方法')
        api.channel
          .updateChannel(params)
          .then(res => {
            // console.log(res.data)
            if (res.data === 'OK') {
              _this.$message({
                type: 'success',
                message: '开启推流成功!'
              })
            } else {
              _this.$message({
                type: 'warning',
                message: '开启推流失败!'
              })
            }
          })
          .catch(res => {
            _this.$message({
              type: 'warning',
              message: '开启推流失败!'
            })
          })
      } else {
        // console.log('调用关闭推流方法')
        api.channel
          .updateChannel(params)
          .then(res => {
            // console.log(res.data)
            if (res.data === 'OK') {
              _this.$message({
                type: 'success',
                message: '关闭推流成功!'
              })
            } else {
              _this.$message({
                type: 'warning',
                message: '关闭推流失败!'
              })
            }
          })
          .catch(res => {
            _this.$message({
              type: 'warning',
              message: '关闭推流失败!'
            })
          })
      }
      this.init()
    },
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.pageSize = val
    },
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      this.currentPage = val
    },
    generateUUID () {
      var d = new Date().getTime()
      var uuid = 'xxxxx-xxxxx-xyxxx'.replace(/[xy]/g, function (c) {
        var r = (d + Math.random() * 16) % 16 | 0
        d = Math.floor(d / 16)
        return (c === 'x' ? r : (r & 0x3) | 0x8).toString(16)
      })
      return uuid
    }
  },
  created () {
    this.init()
    for (let i = 1; i <= 30; i++) {
      this.timeouts.push({ value: i })
    }
  }
}
</script>
